  display: inline-block;
  padding: 1em 1.4em;
  font-weight: bold;
  text-transform: uppercase;
  border: none;
  letter-spacing: 0.1em;
  background-color: #dd3333;
  width: 100%;
  box-shadow: 0 6px 14px rgba(0, 0, 0, 0.19), 0 5px 5px rgba(0, 0, 0, 0.23);
  color: #fff; }
  button:hover {
    cursor: pointer; }
  @media screen and (min-width: 480px) {
    button {
      width: initial; } }
  button:hover {
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19), 0 6px 6px rgba(0, 0, 0, 0.23); }
  button:focus {
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.19), 0 3px 3px rgba(0, 0, 0, 0.23); }

main > section {
  position: relative; }
  main > section div.container {
    width: 100%;
    padding: 0 1.5rem;
    height: 100%;
    margin: auto; }
    @media screen and (min-width: 480px) {
      main > section div.container {
        padding: 0;
        width: 80%; } }

main > h1 {
  margin: 4rem 0 0 1.5rem;
  font-size: 2em;
  position: relative;
  padding-bottom: 1em; }
  @media screen and (min-width: 800px) {
    main > h1 {
      margin: calc(4rem * 2) 0 0 calc((100% - 80%) / 2); }
      main > h1 span {
        color: #cce6ff; } }
  @media screen and (min-width: 480px) {
    main > h1 {
      font-size: 2.8em; } }
